:root {
  --fonte: 'Noto Sans', sans-serif;
  --black: #111111;
  --gray: #292929;
  --dark-gray: #141414;
  --white: #ffffff;
  --val-red: #ff4655
}

body {
  margin: 0;
  font-family: var(--fonte);
  background-color: var(--gray);
  color: var(--white);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
